import "../../styles/contacts.css";

import React        from "react";
import EmailIcon    from "../../media/email.svg"
import GitHubIcon   from "../../media/github.svg"
import LinkedInIcon from "../../media/linkedin.svg"

class ContactInfo
{
    label   = "";
    image   = "";
    display = "";
    link    = "";

    constructor(label, image, display, link)
    {
        this.label   = label;
        this.image   = image;
        this.display = display;
        this.link    = link;
    }
}

class Contacts extends React.Component
{
    contact =
    [
        new ContactInfo("Email"   , EmailIcon   , "gabrielle@gtsc.dev"     , "mailto:gabrielle@gtsc.dev"                           )
      , new ContactInfo("GitHub"  , GitHubIcon  , "XxGTSCxX"               , "https://github.com/XxGTSCxX"                         )
      , new ContactInfo("LinkedIn", LinkedInIcon, "gabrielle-tan-suan-choo", "https://www.linkedin.com/in/gabrielle-tan-suan-choo/")
    ];

    render()
    {
        const contact_list = this.contact.map(
            (element) =>
            <a className="Contact" href={element.link} target="_blank" rel="noopener noreferrer">
                <img src={element.image} alt={this.label}></img>
                {element.display}
            </a>
        );

        return (
            <div className="Contacts">
                {contact_list}
            </div>
        );
    }
}

export default Contacts;
