import "../styles/contact_me.css";
import React    from "react";
import Contacts from "./templates/contacts.js";

class ContactMe extends React.Component
{
    id = "ContactMe";

    toggleWindow = () =>
    {
        const self = document.getElementById(this.id);

        if (self)
        {
            const contacts = self.getElementsByClassName("Contacts");

            for (let i = 0; i < contacts.length; ++i)
            {
                if (contacts[i].classList.contains("Active"))
                {
                    contacts[i].classList.remove("Active");
                    contacts[i].style.maxHeight = null;
                }
                else
                {
                    contacts[i].classList.add("Active");
                    contacts[i].style.maxHeight = contacts[i].scrollHeight + "px";
                }
            }
        }
    }

    render()
    {
        return (
            <div id={this.id} className="ContactMe">
                <div className="Tab" onClick={this.toggleWindow}>
                    Contact Me
                </div>
                <Contacts />
            </div>
        );
    }
}

export default ContactMe;
